// 转数字
export function valToNum(val: string | number) {
  val = String(val);
  let [index, len, firstIsZero, firstIsMinus, hasPoint] = [0, val.length, false, false, false];
  while (index < len) {
    let currChar = val[index];
    switch (index) {
      case 0:
        if (!/[\d-]/.test(currChar)) {
          // 第一位数字和-
          return "";
        }
        if (currChar === "0") {
          firstIsZero = true;
        } else if (currChar === "-") {
          firstIsMinus = true;
        }
        break;
      case 1:
        if (currChar === ".") {
          // 不允许 -.
          if (firstIsMinus) {
            return "-";
          }
          hasPoint = true;
        } else if (firstIsZero && currChar !== ".") {
          // 只允许 0.
          return "0";
        } else if (!/[\d]/.test(currChar)) {
          return val.substr(0, index);
        }
        break;
      default:
        if (currChar === ".") {
          if (hasPoint) {
            return val.substr(0, index); // 只有一个小数点
          }
          hasPoint = true;
        } else if (!/\d/.test(currChar)) {
          return val.substr(0, index);
        }
    }
    index++;
  }
  return val;
}

// 转正数
export function valToPositive(val: string | number) {
  val = valToNum(val);
  return val.charAt(0) === "-" ? val.substr(1) : val;
}

// 转负数
export function valToNegative(val: string | number) {
  val = valToNum(val);
  return val.charAt(0) !== "-" ? "-" : val;
}

// 转小数 默认四位小数 可指定精度 accuracy
export function valToDecimal(val: string | number, accuracy: number = 4) {
  val = valToNum(val);
  if (accuracy === 0 || accuracy === null) {
    accuracy = -1;
  } else if (accuracy < 0) {
    return val;
  }
  let pointP = val.indexOf(".");
  return pointP !== -1 ? val.substr(0, pointP + (accuracy + 1)) : val;
}

// 转整数
export function valToInt(val: string | number) {
  return valToDecimal(val, 0);
}
