 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import CodeRulesConfig from "@/microApps/codeRuleManage/components/CodeRulesConfig.vue";
import { ElForm } from "element-ui/types/form";
import api from "@/microApps/codeRuleManage/api";
import genCascader from "@/utils/genCascader";

@Component({
  components: {
    CodeRulesConfig
  }
})
export default class CodeRuleConfig extends Vue {
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "codeName"
    }
  };
  // 选中表单信息
  isEdit: boolean = false;
  form: any = {};
  formRules: any = {
    codeName: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };
  loading: boolean = false;
  parentNode: any = null;
  baseTypeOptions: any[] = [];
  autoFind: boolean = true;

  // 右侧标题
  get rightTitle() {
    if (this.isEdit) {
      return this.form.id ? `编辑${this.form.codeName}` : "添加编码规则";
    }
    return `${this.form.codeName || ""}详情`;
  }

  created(): void {
    this.getRuleTree();
    // this.getBaseTypeList();
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.form = JSON.parse(JSON.stringify(data));
    this.isEdit = false;
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }

  // 获取规则树
  async getRuleTree() {
    let {
      data: { status, data, message }
    } = await this.$http.get(api.getRuleTree);
    if (status) {
      this.treeMessage.data = data;
      if (this.autoFind && data.length) {
        this.form = data[0];
        this.autoFind = false;
      }
      if (this.form.id) {
        this.$nextTick(() => {
          (this.$refs.tree as any).setCurrentKey(this.form.id);
        });
      }
    } else {
      this.$message.error(message);
    }
  }

  // 添加
  handleAddRule(parent: any) {
    this.isEdit = true;
    this.form = parent ? { parentId: parent.id, parentName: parent.codeName } : {};
    this.$nextTick(() => {
      (this.$refs.form as ElForm).clearValidate();
    });
    this.parentNode = parent;
  }
  handleSaveRule() {
    (this.$refs.form as ElForm).validate(async flag => {
      if (flag) {
        let expressionResult = await (this.$refs.codeRule as any).getResult();
        if (!expressionResult.ruleExpress) {
          return this.$message.info("规则表达式不能为空");
        }
        this.saveRule(expressionResult);
      }
    });
  }
  // 修改
  handleEditRule(item: any) {
    this.form = JSON.parse(JSON.stringify(item));
    this.$nextTick(() => {
      (this.$refs.form as ElForm).clearValidate();
      (this.$refs.tree as any).setCurrentKey(this.form.id);
    });
    this.isEdit = true;
  }
  // 保存
  async saveRule(expressionResult: any) {
    this.loading = true;
    try {
      let {
        data: { status, message }
      } = await this.$http.post(this.form.id ? api.updateRuleTree : api.addRuleTree, {
        id: this.form.id,
        code: this.form.code,
        codeName: this.form.codeName,
        parentId: this.form.parentId,
        remark: this.form.remark,
        ...expressionResult
      });
      if (status) {
        this.$message.success("保存成功");
        this.isEdit = false;
        this.getRuleTree();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }
  // 删除
  handleDeleteRule(item: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.doDeleteRule(item.id);
    });
  }
  async doDeleteRule(id: string) {
    let {
      data: { status, message }
    } = await this.$http.get(api.deleteRuleTree + id);
    if (status) {
      this.$message.success("删除成功");
      this.autoFind = true;
      this.getRuleTree();
    } else {
      this.$message.error(message);
    }
  }
  // 基础对象类型
  async getBaseTypeList() {
    let {
      data: { status, data, message }
    } = await this.$http.get(api.getBaseTypeList);
    if (status) {
      data.forEach((item: any) => {
        item.disabled = true;
      });
      genCascader(data);
      this.baseTypeOptions = data;
    } else {
      this.$message.error(message);
    }
  }
}
