import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { valToInt, valToPositive } from "@/utils/inputFormat";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class CodeRulesConfig extends Vue {
  @Prop({ type: String })
  expressionJson?: string;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;
  @Prop({ type: Boolean, default: true })
  headerVisible!: boolean;
  // 自定义按键
  keyboardFocus: boolean = false;
  widgetFocus: boolean = false;
  cursorIndex: number | null = null;
  // 行政区域级别
  areaOptions: any[] = [
    { label: "国家", value: "COUNTRY" },
    { label: "省级", value: "PROVINCE" },
    { label: "市级", value: "CITY" },
    { label: "区县", value: "DISTRICT" }
    // { label: "街道", value: "STREET" },
  ];
  // 连接符
  concatOptions: any[] = [
    { label: "短横线", value: "-" },
    { label: "下划线", value: "_" }
    // { label: "自定义", value: "custom" }
  ];
  // 序号类型
  indexTypeOptions: any[] = [
    { label: "数字", value: "NUM" },
    { label: "大写字母", value: "UPPER_CASE" },
    { label: "小写字母", value: "LOWER_CASE" }
  ];
  keyboardList: any[] = [
    { type: "area", label: "行政区域", value: [] },
    { type: "baseData", label: "基础数据类型", value: 0, maxlength: 4 },
    { type: "concat", label: "连接符", selectVal: 0, value: "" },
    { type: "index", label: "序号", typeValue: 0, sortValue: 0, maxlength: 4 },
    { type: "time", label: "时间", value: "" },
    { type: "fixedField", label: "固定字段", selectVal: 0, value: "" }
  ];
  form: { selectConditions: any[] } = {
    selectConditions: []
  };
  formRules: any = {};

  // 输入限制
  hasIndex: boolean = false;

  // 预览表达式
  get preRuleExpress() {
    return this.form.selectConditions
      .map(item => {
        switch (item.type) {
          case "area":
            return `${item.label}(${item.value.map((d: any) => this.areaOptions[d].label).join("，")})`;
          case "baseData":
            return `${item.label}(父节点层级${item.value}，最大长度${item.maxlength})`;
          case "concat":
            return this.concatOptions[item.selectVal].value;
          case "index":
            return `${item.label}(${this.indexTypeOptions[item.typeValue].label}，最大长度${item.maxlength})`;
          case "time":
          case "fixedField":
            return `${item.label}(${item.value})`;
        }
      })
      .join("");
  }

  // 后端真实表达式
  get ruleExpress() {
    return this.form.selectConditions
      .map(item => {
        switch (item.type) {
          case "area":
            return `${item.label}("${item.value.map((d: any) => this.areaOptions[d].value).join(",")}")`;
          case "baseData":
            return `${item.label}(${item.value},${item.maxlength})`;
          case "concat":
            return `连接符("${this.concatOptions[item.selectVal].value}")`;
          case "index":
            return `${item.label}("${this.indexTypeOptions[item.typeValue].value}",${item.maxlength})`;
          case "time":
          case "fixedField":
            return `${item.label}("${item.value}")`;
        }
      })
      .join("+");
  }

  @Watch("expressionJson", { immediate: true })
  handelExpressionJsonChange(expressionJson: string) {
    if (expressionJson) {
      this.form.selectConditions = JSON.parse(expressionJson);
      this.hasIndex = this.form.selectConditions.some((d: any) => d.type === "index");
    } else {
      this.form.selectConditions = [];
    }
  }

  created() {
    document.addEventListener("click", this.handleBlurRuleInput, true);
    document.addEventListener("keydown", this.handleKeydown);
  }

  destroyed() {
    document.removeEventListener("click", this.handleBlurRuleInput);
    document.removeEventListener("keydown", this.handleKeydown);
  }

  // 规则配置输入操作
  handleFocusRuleInput(index: number | null) {
    if (this.disabled) return;
    this.keyboardFocus = true;
    this.cursorIndex = index;
  }
  handleBlurRuleInput() {
    this.keyboardFocus = false;
  }
  handleFocusWidgetInput() {
    this.widgetFocus = true;
  }
  handleBlurWidgetInput() {
    this.widgetFocus = false;
  }

  get reactKeydownEvent() {
    return this.keyboardFocus && !this.widgetFocus;
  }

  handleKeydown(e: KeyboardEvent) {
    if (!this.reactKeydownEvent) return;
    switch (e.code.toLocaleLowerCase()) {
      case "backspace":
        this.handleDeleteToken();
        break;
      case "arrowleft":
        this.handleMoveCursor(false);
        break;
      case "arrowright":
        this.handleMoveCursor(true);
        break;
    }
  }
  handleSelectKey(item: any) {
    let data = JSON.parse(JSON.stringify(item));
    if (this.cursorIndex === null) {
      this.form.selectConditions.push(data);
    } else {
      this.form.selectConditions.splice(this.cursorIndex, 0, data);
      this.cursorIndex++;
    }
    switch (item.type) {
      case "index":
        this.hasIndex = true;
        break;
    }
  }
  // 删除一个表达式单元
  handleDeleteToken() {
    let item: any;
    if (this.cursorIndex === null) {
      item = this.form.selectConditions.pop();
    } else if (this.cursorIndex > 0) {
      item = this.form.selectConditions.splice(this.cursorIndex - 1, 1)[0];
    }
    switch (item.type) {
      case "index":
        this.hasIndex = false;
        break;
    }
  }

  // 光标移动
  handleMoveCursor(arrow: boolean) {
    if (arrow) {
      if (this.cursorIndex !== null && this.cursorIndex < this.form.selectConditions.length - 1) {
        this.cursorIndex++;
      } else if (this.cursorIndex === this.form.selectConditions.length - 1) {
        this.cursorIndex = null;
      }
    } else {
      if (this.cursorIndex === null) {
        this.cursorIndex = this.form.selectConditions.length - 1;
      } else if (this.cursorIndex > 0) {
        this.cursorIndex--;
      }
    }
  }

  // 获取输入结果
  getResult() {
    return new Promise((resolve, reject) => {
      (this.$refs.form as ElForm).validate(flag => {
        if (flag) {
          resolve({
            preRuleExpress: this.preRuleExpress,
            ruleExpress: this.ruleExpress,
            preRuleExpressJson: JSON.stringify(this.form.selectConditions)
          });
        } else {
          reject("表单校验失败");
        }
      });
    });
  }

  // 输入校验
  handleInputMaxLength(item: any, val: string) {
    item.maxlength = valToPositive(valToInt(val));
  }

  // 日期格式化输入限制
  handleInputDateStr(item: any, val: string) {
    for (let i = 0; i < val.length; i++) {
      if (!/[yMmDdHhSs.:-]/.test(val[i])) {
        item.value = val.substr(0, i);
        break;
      }
    }
  }

  // 固定字段格式化
  handleInputFixed(item: any, val: string) {
    for (let i = 0; i < val.length; i++) {
      if (!/[a-zA-Z0-9._-]/.test(val[i])) {
        item.value = val.substr(0, i);
        break;
      }
    }
  }
}
